@import url('https://fonts.googleapis.com/css2?family=Young+Serif:wght@400&family=Outfit:wght@400;600;700&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Outfit', sans-serif;
  font-size: 16px;
}

/* Aplicar la fuente 'Young Serif' a elementos espec√≠ficos */
h1, h2, h3 {
  font-family: 'Young Serif', serif;
}

h1{
  color: #302D2B;
  font-size: 2.5rem;
  font-weight: 500;
}

h2{
  color: #854532;
}

/* Aplicar diferentes pesos de la fuente 'Outfit' */
p {
  font-family: 'Outfit', sans-serif;
  font-weight: 400; /* Peso normal */
  color: #5F564E;
}

strong {
  font-family: 'Outfit', sans-serif;
  font-weight: 700; /* Peso en negrita */
  color: #5F564E;
}

